.center {
  margin: auto;
}

body {
  height: 100%;
  margin: 0;
  padding-top: 65px;
}
.mainContainerWidth {
  min-width: 500px;
  max-width: 1700px;
}

.full-height {
  height: 100%;
  background: url("../Img/MainImage/MainBgLogo.png") no-repeat center center
    fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

/* BackGrounColor */
.bgGrad {
  background: rgba(16, 113, 192, 0.767);
}
.bgMain {
  background: rgba(150, 147, 117, 0.767);
}
.bgCol {
  background-color: rgba(0, 228, 236, 0.705);
}
.bgHigh {
  background-color: rgba(236, 169, 216, 0.705);
}
.bgLib {
  background-color: rgba(83, 121, 204, 0.705);
}
.bgC {
  background-color: rgba(177, 228, 36, 0.705);
}
.bgAp {
  background-color: rgba(83, 240, 21, 0.705);
}
.bgFooter {
  background-color: rgba(73, 214, 18, 0.705);
}
.bgGallery {
  background: rgba(185, 163, 42, 0.705);
}
.centered {
  height: 100vh; /* Magic here */

  justify-content: center;
  align-items: center;
}

.mheader {
  padding: 156px 0 100px;
}

.msection {
  padding: 300px 0;
}
.imgTable {
  width: 190px;
  height: 190px;
}

.tbh {
  flex: 2;
}
.asText {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}
.bgApp {
  background: linear-gradient(0deg, #528548 0, #19e6db 100%);
  background-size: cover; /* <------ */
  background-repeat: no-repeat;
  background-position: center center;
}
.bgTb {
  background: rgb(red, green, blue);
  background-size: 100% auto; /* <------ */
  background-repeat: no-repeat;
  background-position: center center;
}
/*------------------Login---------------*/
.logIn {
  height: 100vh; /* Magic here */
  padding-top: 100px;
  justify-content: center;
  align-items: center;
}
.form {
  background: rgba(42, 185, 102, 0.7);
  padding-top: 40px;

  margin: auto;

  height: 400px;
  width: 295px;
  text-align: center;
  border-radius: 50px;

  font-family: "Times New Roman", sans-serif;
  font-size: 16;
  font-weight: 200px;
  padding: 50px 0;
  transition: border 0.5s;
  outline: none;
  color: #fff;
}
.logInInput {
  text-align: center;
  justify-content: center;
  width: 250px;
}
.registerForm {
  height: 100vh; /* Magic here */

  justify-content: center;
  align-items: center;
}
/*-------Programs-----------------*/
.ProgramControl {
  padding: 156px 0 100px;
}
/*Table---ScrollBar-----*/
.my-custom-scrollbar {
  position: relative;
  height: 200px;
  overflow: auto;
}
.table-wrapper-scroll-y {
  display: block;
}
/*========HideButton===========*/
.hide {
  display: none;
}

/*=========Container width==========*/

@media (min-width: 1200px) {
  .container-small {
    width: 1300px;
  }
  .container-large {
    width: 1500px;
  }
}

.container-small,
.container-large {
  max-width: 80%;
}

/*=========OverFlow=========*/
.anyClass {
  overflow-y: scroll;
}
