.bsSsHeader {
  position: relative;
  overflow: hidden;
  padding-top: calc(15rem + 400px);
  padding-bottom: 7rem;
  background: url("../Img/BsSs/Main.JPG") no-repeat center center scroll;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
